.landing-page {
    width: 100vw;
    -ms-width: 100%;
    height: 100vh;
    background: url('../../assets/stan-lee-front-door.jpg');
    background-position: center;
    background-size: cover;
    position: relative;
    
    // -ms-height: 100%;
    // -ms-background: url('../../assets/stan-lee-front-door.jpg');
    // -ms-background-position: center;
    // -ms-background-size: cover;
    // -ms-background-repeat: no-repeat;

    .enter-button {
        position: absolute;
        text-align: center;
        width: 7rem;
        height: 2.5rem;
        top: 50%;
        left: 50%;
        margin-left: -3.5rem;
        margin-top: -1.25rem;
        // padding: 7.5px 15px;
        text-decoration: none;
        text-transform: uppercase;
        font-size: 24px;
        background: linear-gradient(to bottom, rgba(128, 62, 1, .5), rgba(196, 168, 14, .5));
        border: 1px solid black;
        border-radius: 5px;
        color: black;
        display: flex;
        justify-content: center;
        align-items: center;
        

        &:hover {
            color: white;
        }
    }
}